export const RequestHeaders = ({ useFormRef, ...props }) => {
  const [request, setRequest, onRequestChange] = useFormRef;
  let useFormRefHeader = [
    request.headers,
    (headers) => {
      setRequest({ ...request, headers })
    },
    onRequestChange
  ]
  return <PropValue useFormRef={useFormRefHeader} parent="headers" {...props} />
}

