
import { localMap } from "_/hooks/storeMap";
const INIT_HEADERS = [
    { name: 'Content-Type', value: 'application/json' },
    { name: 'Accept', value: 'application/json' },
    { name: 'Authentication', value: "Bearer #{TOKEN}" }
]
const INIT_OPTIONS = [
    { name: 'method', value: ['', 'GET', 'POST', 'PUT', 'DELETE'] },
    { name: 'content-type', value: ['', 'application/json', 'application/xml'] }
]
const INIT_PARAMS = [
    { name: 'TOKEN', value: 'eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJzdWIiOiIxMjM0NTY3ODkwIiwibmFtZSI6IkpvaG4gRG9lIiwiaWF0IjoxNTE2MjM5MDIyfQ.SflKxwRJSMeKKF2QT4fwpMeJf36POk6yJV_adQssw5c' },
]
const INIT_SERVICES = [
    { name: 'Xxxx', description: 'xxxxxxx', target: 'http://127.0.0.1:3000/api', path: '/auth/login' },
    { name: 'Yyyy', description: 'yyyyyyy', target: 'http://127.0.0.1:3000/api', path: '/auth/logout' }
]

const createStore = (KEY, INIT) => {
    let value = localMap.get(KEY, INIT)
    return {
        values: () => {
            value = localMap.get(KEY, INIT)
            return value
        },
        findId: (id) => {
            id = parseInt(id)
            return value.filter(it => it.id === id)
        },
        create: (data = {}) => {
            data.id = parseInt(data.id) || new Date().getTime()
            localMap.put(KEY, [...value, data])
        },
        update: (id, data = {}) => {
            id = parseInt(id)
            data.id = id
            value = value.map(it => it.id === id ? data : it)
            localMap.put(KEY, value)
        },
        remove: (id) => {
            id = parseInt(id)
            value = value.filter(it => it.id !== id)
            localMap.put(KEY, value)
        },
        bulk: (newValue) => {
            newValue = newValue.map(it => {
                it.id = parseInt(it.id) || new Date().getTime()
                return it;
            })
            localMap.put(KEY, newValue)
            value = newValue
        }
    }
}

export const headerStore = createStore('global-headers', INIT_HEADERS);
export const optionStore = createStore('global-options', INIT_OPTIONS);
export const paramStore = createStore('global-params', INIT_PARAMS);
export const serviceStore = createStore('global-services', INIT_SERVICES);
