import React, { useEffect } from 'react'
import { RestService } from '_/components'
import { useForm } from '_/hooks/useForm'
import { serviceStore } from '_/store'

export const List = () => {
    const [value, setValue, onChange] = useForm([])
    useEffect(() => {
        const value = serviceStore.values()
        setValue(value)
    }, [setValue])
    return (
        <div>
            {
                value.map((it, ix) => (
                    <div className="my-2">
                        <RestService value={it}
                            onChange={onChange}
                            parent={ix} />
                    </div>
                ))
            }
        </div>
    )
}
