import React from 'react'

export const RestService = ({ value, onChange, parent, children, disabled }) => {
    const { aaaaaaa = 'XXXX' } = {}
    children = children ? <div className="align-self-center btn-group">{children}</div> : null
    parent = parent ? parent + '.' : ''
    return (
        <div className="media">
            <div className="align-self-center mr-2">
                <i className="h1 m-0 fa fa-server"></i>
            </div>
            {
                disabled ?
                    <div className="media-body">
                        <div className="h5 m-0 font-weight-bold">{value.name}</div>
                        <div>{value.description}</div>
                    </div>
                    :
                    <div className="media-body mr-2">
                        <input name={parent + "name"}
                            value={value.name}
                            onChange={onChange}
                            className="form-control form-control-md font-weight-bold" />
                        <input name={parent + "description"}
                            value={value.description}
                            onChange={onChange}
                            className="form-control form-control-sm" />
                    </div>
            }
            {children}
        </div>
    )
}

export const RestTarget = ({ value, onChange, parent, children, disabled }) => {
    const methods = ['GET', 'POST']
    children = children ? <div className="input-group-append">{children}</div> : null
    parent = parent ? parent + '.' : ''
    return (
        <div className="input-group mb-3">
            <div className="input-group-prepend">
                <select className="custom-select custom-select-lg font-weight-bold text-danger"
                    name={parent + "method"}
                    value={value.method}
                    onChange={onChange}
                    disabled={disabled}>
                    {methods.map(it => <option key={it}>{it}</option>)}
                </select>
            </div>
            <input type="text"
                className="form-control form-control-lg font-weight-bold  text-primary"
                name={parent + "target"}
                value={value.target}
                onChange={onChange}
                disabled={disabled}
            />
            <input type="text"
                className="form-control form-control-lg font-weight-bold  text-primary"
                name={parent + "path"}
                value={value.path}
                onChange={onChange}
                disabled={disabled}
            />
            {children}
        </div>
    )
}